const errorCodes = {
  updateOrderFailed: ',',
  noSelected: '',
};

export default (swal, errorCode) => {
  if (errorCodes[errorCode]) {
    swal({
      icon: 'error',
      title: '',
      text: errorCodes[errorCode],
    });
  } else {
    console.error(errorCode);
    swal({
      icon: 'error',
      title: '',
      text: 'sorry',
    });
  }
};
